
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'


import serviceList from '../../components/ServiceList';
import moduleTitle from '../../components/ModuleTitle';
import tabBar from '../../components/TabBar';
import searchBar from '../../components/SearchBar';
import BScroll from 'better-scroll';
export default {
name:'provideServices',
components:{Flexbox, FlexboxItem,serviceList,moduleTitle,Toast,tabBar,searchBar},
data(){
return {
showToast:false,
toastMsg:'',
tempService:[],
pickerSortArr:[],//['心血管','耳鼻喉','高血压','减重','糖尿病'],
pickerContentArr:[],//['心血管','耳鼻喉','高血压','糖尿病']
dictionary:{},
searchObj:{},
pageNumber:1,
pageSum:0,
times: 0,
loading: false,
loadable:false,
pullUpObj:{
type:0,
text:'松开立即加载'
},// 0 初始化 1可以加载 2最后一页
}
},
mounted(){
var vm = this;
this.initDictionary();
this.init();
},
methods:{
init(){
var vm = this;
let data = {pageNumber:this.pageNumber,pageSize:10,status:2,serviceAttribute:1};
if(Object.keys(this.searchObj).length>0){
for(let key in this.searchObj){
data[key] = this.searchObj[key];
}
}
this.$http.post('/service/query',data).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.items && data.items.length==0){
this.showToast = true;
this.toastMsg = '未查询到对应的数据';
if(this.pullUpObj.type == 0){
this.tempService = [];
}
return ;
}
this.pageSum = data.pagesCount;
this.loading = false;
this.loadable = (this.pageSum>1);
if(this.pullUpObj.type == 0){
this.tempService = data.items;
this.pullUpObj.type = 1;
}else{
this.tempService = [...this.tempService,...data.items];
}
}else{
this.loading = false;
this.loadable = false;
console.log(data.resultCode);
this.showToast = true;
this.toastMsg = data.resultCode.message;
}
this.toastMsg = '';
},(error) => {
console.log(error);
this.showToast = true;
this.toastMsg = '查询失败';
console.log(data);
});
},
nextPage(){
if(!(this.pageSum==this.times)){
this.times += 1;
this.pageNumber += 1;
console.log('nextPage');
this.init();
}else{
this.pullUpObj.type = 2;
this.pullUpObj.text = '已经到底了'
}
},
reLoad(){
this.pullUpObj = {
type:0,
text:'松开立即加载'
};
this.times = 0;
this.pageNumber = 1;
this.init();
},
search(value){
console.log('-=-=',value);
this.searchObj = {name:value.value,serviceType:value.type!="全部"?value.type:''};
this.pageNumber = 1;
this.pageSum = 0;
this.times = 0;
this.$set(this.pullUpObj,'type',0);

this.init();
},
initDictionary(){
let vm = this;

this.$http.get('/api/contracte/queryDictionary?typeId=31',{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
this.dictionary = data.data;
for(let obj of data.data){
this.pickerSortArr.push({itemId:obj.itemId,itemName:obj.itemName});
}
}else{
console.error(data);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.error(error);
vm.showToast = true;
vm.toastMsg = '操作失败';
})
}
}
}



